.dog-profile-card-wrapper {
  display: flex;
  justify-content: center;
  width: 50vw;
  height: 100vh;
}
.dog-profile-card {
  width: 80%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}
.dog-profile-card h1 {
  text-align: start;
  font-size: 70px;
}
.dog-profile-card .dog-profile-image {
  width: auto;
  min-height: 50vh;
  border-radius: 10px;
  border: 1px solid black;
  box-shadow: inset 0px 0px 30px 10px black;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 40px;
}
.dog-profile-card p {
  font-size: 24px;
  width: auto;
  height: 50vh;
  text-align: start;
}

.dog-application-submission {
  display: flex;
}

.application-form {
  margin: auto;
  text-align: left;
  border: 1px solid black;
  border-radius: 10px;
  padding: 20px;
  width: 30%;
}
